}
.ff-serif .wrap-content .nav-main {
  font-family: "Times New Roman, Time, serif";
  font-weight: 600;
}
.ff-serif .wrap-content .footer h5 {
  font-family: "Times New Roman, Time, serif";
  font-weight: 700;
}
.ff-serif .wrap-content .footer a {
  font-family: "Times New Roman, Time, serif";
}
.ff-serif .wrap-content .wrap-sitemap .level-1 > li > a {
  font-family: "Times New Roman, Time, serif";
  font-weight: 700;
