/* hide-img */
.hide-image .wrap-img,
.hide-image .wrap-owl-slider-main,
.hide-image .first-person,
.hide-image .wrap-content img {
  display: none!important;
}
.hide-image .video-list .col-sm-6 {
  text-align: left;
}
/* char-interval */
.interval-small .wrap-content {
  letter-spacing: normal;
}
.interval-medium .wrap-content {
  letter-spacing: .1em;
}
.interval-large .wrap-content {
  letter-spacing: .3em;
}
