@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?tlg13x');
  src:  url('../fonts/icomoon.eot?tlg13x#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?tlg13x') format('truetype'),
    url('../fonts/icomoon.woff?tlg13x') format('woff'),
    url('../fonts/icomoon.svg?tlg13x#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}


.icom {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  display: inline-block;
  vertical-align: middle;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-answer:before {
  content: "\e900";
}
.icon-question:before {
  content: "\e901";
}
.icon-organization:before {
  content: "\e902";
}
.icon-printer:before {
  content: "\e903";
}

